.content-container {
  position: relative;
}

@media only screen and (max-width: 1080px) {
  .content-container {
    margin-bottom: 80px;
    overflow-y: hidden;
    overflow-x: hidden;
  }
}

@media only screen and (min-width: 1080px) {
  .content-container {
    margin-left: 80px;
  }
}
