@import "../../base";

.homepage {
  background: url("../../assets/showcase.jpg") no-repeat center center/cover;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgb(2, 0, 36);
    background: linear-gradient(
      0deg,
      rgba(2, 0, 36, 1) 0%,
      rgba(0, 0, 0, 1) 0%,
      rgba(0, 4, 4, 0) 95%
    );
  }

  & * {
    z-index: 10;
  }

  &__content {
    font-size: 5rem;
    color: $color-full-white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 0 20px;

    p {
      text-transform: uppercase;
      font-weight: bold;
      margin-bottom: 3rem;
    }
  }

  @media only screen and (max-width: 1080px) {
    height: calc(100vh - 80px);

    &__content {
      font-size: 3rem;
    }
  }
}
